#headNav {
  height: 50px;
  background: #444;
  color: #eee;
  display: flex;
}
#headNav .nav-logo-wrap {
  line-height: 50px;
  margin: 0 auto;
}
#headNav .nav-list-wrap {
  width: calc(100% - 50px);
  display: flex;
  flex-direction: row-reverse;
}
#headNav .nav-list-wrap .ant-menu {
  background: transparent;
  border: none;
}
#headNav .nav-list-wrap .ant-menu .ant-menu-item:not(.ant-menu-item-selected) > a {
  color: #eee;
}
